@font-face {
  font-family: 'social';
  src: url('../font/social.eot?59324301');
  src: url('../font/social.eot?59324301#iefix') format('embedded-opentype'),
       url('../font/social.woff2?59324301') format('woff2'),
       url('../font/social.woff?59324301') format('woff'),
       url('../font/social.ttf?59324301') format('truetype'),
       url('../font/social.svg?59324301#social') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
